"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/router";
import Link from "next/link";

const CharactersPage = () => {
  const router = useRouter();
  const { id } = router.query;

  const [characters, setCharacters] = useState<any[]>([]);
  const [filteredCharacters, setFilteredCharacters] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState("");
  const [roleFilter, setRoleFilter] = useState("all");

  useEffect(() => {
    async function loadCharacters() {
      if (!id) return;
      setLoading(true);
      try {
        const res = await fetch(`https://api.jikan.moe/v4/anime/${id}/characters`);
        const json = await res.json();
        const data = json.data || [];
        setCharacters(data);
        setFilteredCharacters(data);
      } catch (error) {
        console.error("Erro:", error);
      } finally {
        setLoading(false);
      }
    }
    loadCharacters();
  }, [id]);

  useEffect(() => {
    let results = characters;
    
    if (searchTerm.trim() !== "") {
      const term = searchTerm.toLowerCase();
      results = results.filter(character => 
        character.character.name.toLowerCase().includes(term) ||
        character.role.toLowerCase().includes(term) ||
        character.voice_actors?.some((va: any) => 
          va.person.name.toLowerCase().includes(term)
        )
      );
    }
    
    if (roleFilter !== "all") {
      results = results.filter(character => 
        character.role.toLowerCase() === roleFilter.toLowerCase()
      );
    }
    
    setFilteredCharacters(results);
  }, [searchTerm, roleFilter, characters]);

  const uniqueRoles = Array.from(
    new Set(characters.map(c => c.role.toLowerCase()))
  ).sort();

  return (
    <div
      style={{
        width: "100%",
        minHeight: "100vh",
        backgroundImage: "url('/detail.jpg')",
        backgroundSize: "cover",
        backgroundPosition: "center",
        backgroundAttachment: "fixed",
        padding: "20px",
      }}
    >
      <div style={{ maxWidth: "1200px", margin: "0 auto" }}>
        <div style={{ 
          display: "flex", 
          justifyContent: "space-between", 
          alignItems: "center", 
          marginBottom: 30,
          flexWrap: "wrap",
          gap: 15
        }}>
          <h1 style={{ color: "white", margin: 0 }}>Personagens</h1>
          <Link href={`/${id}`}>
            <button style={{ 
              padding: "10px 20px", 
              background: "#333", 
              color: "white", 
              border: "none", 
              borderRadius: "5px", 
              cursor: "pointer",
              fontWeight: "bold"
            }}>
              ↩ Voltar ao Anime
            </button>
          </Link>
        </div>

        <div style={{ 
          background: "rgba(0, 0, 0, 0.6)", 
          padding: "20px", 
          borderRadius: "10px",
          marginBottom: "30px"
        }}>
          <div style={{ 
            display: "flex", 
            flexDirection: "column",
            gap: "15px"
          }}>
            <div style={{ display: "flex", gap: "15px", flexWrap: "wrap" }}>
              <input
                type="text"
                placeholder="Buscar por nome, papel ou dublador..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                style={{
                  padding: "12px 15px",
                  borderRadius: "8px",
                  border: "1px solid #555",
                  background: "rgba(0, 0, 0, 0.7)",
                  color: "white",
                  flex: "1",
                  minWidth: "250px",
                  outline: "none",
                  fontSize: "14px"
                }}
              />
              
              <select
                value={roleFilter}
                onChange={(e) => setRoleFilter(e.target.value)}
                style={{
                  padding: "12px 15px",
                  borderRadius: "8px",
                  border: "1px solid #555",
                  background: "rgba(0, 0, 0, 0.7)",
                  color: "white",
                  minWidth: "150px",
                  outline: "none",
                  cursor: "pointer"
                }}
              >
                <option value="all">Todos os papéis</option>
                {uniqueRoles.map((role, index) => (
                  <option key={index} value={role}>
                    {role.charAt(0).toUpperCase() + role.slice(1)}
                  </option>
                ))}
              </select>
            </div>
            
            <div style={{ 
              display: "flex", 
              justifyContent: "space-between",
              alignItems: "center",
              color: "white",
              fontSize: "14px",
              flexWrap: "wrap",
              gap: "10px"
            }}>
              <div>
                <span style={{ color: "#aaa" }}>
                  {filteredCharacters.length} personagem{filteredCharacters.length !== 1 ? 's' : ''} encontrado{filteredCharacters.length !== 1 ? 's' : ''}
                </span>
                {searchTerm && (
                  <span style={{ marginLeft: "10px", color: "#90caf9" }}>
                    para "{searchTerm}"
                  </span>
                )}
                {roleFilter !== "all" && (
                  <span style={{ marginLeft: "10px", color: "#81c784" }}>
                    • Papel: {roleFilter}
                  </span>
                )}
              </div>
              
              {searchTerm && (
                <button
                  onClick={() => setSearchTerm("")}
                  style={{
                    padding: "5px 12px",
                    background: "#555",
                    color: "white",
                    border: "none",
                    borderRadius: "5px",
                    cursor: "pointer",
                    fontSize: "12px"
                  }}
                >
                  Limpar busca
                </button>
              )}
            </div>
          </div>
        </div>

        {loading ? (
          <p style={{ color: "white", textAlign: "center", fontSize: "18px" }}>Carregando personagens...</p>
        ) : filteredCharacters.length === 0 ? (
          <div style={{ 
            textAlign: "center", 
            padding: "40px",
            background: "rgba(0, 0, 0, 0.6)",
            borderRadius: "10px"
          }}>
            <p style={{ color: "white", fontSize: "18px" }}>
              {searchTerm || roleFilter !== "all" 
                ? `Nenhum personagem encontrado para "${searchTerm}"${roleFilter !== "all" ? ` com papel "${roleFilter}"` : ''}`
                : "Nenhum personagem encontrado."}
            </p>
            {(searchTerm || roleFilter !== "all") && (
              <button
                onClick={() => {
                  setSearchTerm("");
                  setRoleFilter("all");
                }}
                style={{
                  marginTop: "15px",
                  padding: "8px 20px",
                  background: "#1e88e5",
                  color: "white",
                  border: "none",
                  borderRadius: "5px",
                  cursor: "pointer"
                }}
              >
                Limpar filtros
              </button>
            )}
          </div>
        ) : (
          <div style={{ 
            display: "grid", 
            gridTemplateColumns: "repeat(auto-fill, minmax(250px, 1fr))", 
            gap: 20,
            background: "rgba(0, 0, 0, 0.5)",
            padding: 20,
            borderRadius: 10
          }}>
            {filteredCharacters.map((character: any) => (
              <div key={character.character.mal_id} style={{ 
                background: "rgba(255, 255, 255, 0.1)", 
                borderRadius: 8, 
                padding: 15,
                backdropFilter: "blur(10px)",
                border: "1px solid rgba(255, 255, 255, 0.1)",
                transition: "all 0.3s"
              }}
              onMouseEnter={(e) => {
                e.currentTarget.style.transform = "translateY(-5px)";
                e.currentTarget.style.borderColor = "#1e88e5";
              }}
              onMouseLeave={(e) => {
                e.currentTarget.style.transform = "translateY(0)";
                e.currentTarget.style.borderColor = "rgba(255, 255, 255, 0.1)";
              }}
              >
                <img 
                  src={character.character.images.jpg.image_url} 
                  alt={character.character.name}
                  style={{ 
                    width: "100%", 
                    height: "300px", 
                    objectFit: "cover", 
                    borderRadius: 5,
                    marginBottom: "10px"
                  }}
                />
                <div style={{ color: "white" }}>
                  <h3 style={{ margin: "0 0 5px 0", fontSize: "16px" }}>
                    {character.character.name}
                  </h3>
                  <div style={{ 
                    display: "inline-block",
                    background: character.role.toLowerCase() === "main" ? "#1e88e5" : 
                               character.role.toLowerCase() === "supporting" ? "#43a047" : "#f4511e",
                    color: "white",
                    padding: "3px 8px",
                    borderRadius: "12px",
                    fontSize: "11px",
                    fontWeight: "bold",
                    marginBottom: "8px"
                  }}>
                    {character.role}
                  </div>
                  <p style={{ margin: "5px 0", fontSize: "14px", color: "#aaa" }}>
                    <strong>Voz por:</strong> {character.voice_actors?.[0]?.person?.name || "Não informado"}
                  </p>
                  {character.voice_actors?.[0]?.person?.language && (
                    <p style={{ margin: "0", fontSize: "12px", color: "#777" }}>
                      ({character.voice_actors[0].person.language})
                    </p>
                  )}
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default CharactersPage;